import { createContext, useContext, useState } from "react";
import { initialSession, Session } from "./Session";

export const SessionContext = createContext<[Session, (session: Session) => void]>([initialSession, () => { }]);
export const useSessionContext = () => useContext(SessionContext);

interface SessionContextProviderProps {
    children: React.ReactNode;
}

export const SessionContextProvider: React.FC<SessionContextProviderProps> = (props) => {
    const [sessionState, setSessionState] = useState(initialSession);

    const wrappedSetSessionState = (session: Session) => {
        if (!!session.isAuthenticated) {
            localStorage.setItem('session', JSON.stringify(session));
            console.log('store session');
        }
        setSessionState(session);
    }

    const defaultSessionContext: [Session, typeof wrappedSetSessionState] = [sessionState, wrappedSetSessionState];



    return (
        <SessionContext.Provider value={defaultSessionContext}>
            {props.children}
        </SessionContext.Provider>
    );
}