import React from 'react';
import './css/App.css';
import './css/effects.css';
import Legend from './Legend';
import Teams from './Teams';
import Images from './Images';
import { useQuery } from 'react-query';
import { TeamEvent, query_options, fetchTeamEvents } from '../../util/Query';
// import { QueryClient, QueryClientProvider } from "react-query";

// export const queryClient = new QueryClient();

function StudentApp() {
    const [selectedTeam, setSelectedTeam] = React.useState<string>("");
    // const [blockingInfo, setBlockingInfo] = React.useState<string | undefined>(undefined);
    // const [nonBlockingInfo, setNonBlockingInfo] = React.useState<string | undefined>(undefined);
    const { data: team_events, error: error_events, isError: isError_events, isLoading: isLoading_events } = useQuery<TeamEvent[], Error>(['team_events', selectedTeam], fetchTeamEvents, query_options)
    console.log(team_events);

    let nonBlockingInfo: TeamEvent[] = [];
    let blockingInfo: TeamEvent[] = [];
    // if (isLoading_events) {
    //     nonBlockingInfo = ["Loading events..."];
    // }
    // if (isError_events || !team_events) {
    //     nonBlockingInfo = ["Error loading events!"];
    // }

    if (selectedTeam !== "" && team_events && !isError_events && !isLoading_events) {
        nonBlockingInfo = team_events.filter((event) => (event.team === "global" || event.team === selectedTeam) && (event.blocking === 0 || !event.blocking));
        blockingInfo = team_events.filter((event) => (event.team === "global" || event.team === selectedTeam) && event.blocking === 1);
        // console.log("blockingInfo:", blockingInfo);
        // console.log("nonBlockingInfo:", nonBlockingInfo);
        // console.log("selectedTeam:", selectedTeam);
    }


    return (
        // <QueryClientProvider client={queryClient}>
        <div className={
            team_events &&
            team_events.filter((event) => (event.team === "global" || event.team === selectedTeam) && event.effect).map((event) => event.effect).join(" ")
        }
        >
            <Legend />
            {
                nonBlockingInfo.length > 0 && <div className="info-wrapper">{
                    nonBlockingInfo.map(
                        (event, i) =>
                            <div key={"info_" + i} className="info">
                                <h1>
                                    ⚠️ Warning: {event.name} ⚠️
                                </h1>
                                <p>
                                    {event.description}
                                </p>
                            </div>
                    )
                }</div>
            }
            <Teams selectedTeam={selectedTeam} setSelectedTeam={setSelectedTeam} team_events={team_events ? team_events : []} />
            {
                blockingInfo.length > 0 ? <div className='blockingInfo-wrapper'>{
                    blockingInfo.map(
                        (event, i) =>
                            <div key={"blocking_" + i} className="blockingInfo">
                                <h1>
                                    🔥 Warning: {event.name} 🔥
                                </h1>
                                <p>
                                    {event.description}
                                </p>
                            </div>
                    )
                }</div> : <Images team={selectedTeam} />
            }
        </div>
        // </QueryClientProvider>
    );
}

export default StudentApp;
