import React from 'react';
import { Map, Marker, Point } from "pigeon-maps";
import { fetchImages, fetchLocations, query_options, TeamPos, Image } from '../../util/Query';
import { useQuery } from 'react-query';
import { diffTime, getBgColor, getBorderColor, grayColor, imgColor } from '../../util/Const';
import ColorBox from './Colorbox';
import { Checkbox } from '@material-ui/core';
import { usePersistentState } from 'react-persistent-state';
import "./css/App.css";

export function osm(x: number, y: number, z: number): string {
    const s = String.fromCharCode(97 + ((x + y + z) % 3))
    return `https://${s}.tile.openstreetmap.org/${z}/${x}/${y}.png`
}

export function stamenToner(x: number, y: number, z: number, dpr = 1): string {
    return `https://stamen-tiles.a.ssl.fastly.net/toner/${z}/${x}/${y}${dpr >= 2 ? '@2x' : ''}.png`
}

export function stamenWatercolor(x: number, y: number, z: number, dpr = 1): string {
    return `https://stamen-tiles.a.ssl.fastly.net/watercolor/${z}/${x}/${y}${dpr >= 2 ? '@2x' : ''}.png`
}

export function stamenTerrain(x: number, y: number, z: number, dpr = 1): string {
    return `https://stamen-tiles.a.ssl.fastly.net/terrain/${z}/${x}/${y}${dpr >= 2 ? '@2x' : ''}.jpg`
}

export const maptiler = (apiKey: string, map = 'streets') => (x: number, y: number, z: number, dpr = 1): string => {
    return `https://api.maptiler.com/maps/${map}/256/${z}/${x}/${y}${dpr >= 2 ? '@2x' : ''}.png?key=${apiKey}`
}

// https://pigeon-maps.js.org/docs/installation
function Admin() {
    const { data: positions, error, isError, isLoading } = useQuery<TeamPos[], Error>('team_pos', fetchLocations, query_options)
    const { data: images, error: error_images, isError: isError_images, isLoading: isLoading_images } = useQuery<Image[], Error>('images', fetchImages, query_options)

    const [drawingMap, setDrawingMap, clearDrawingMap] = usePersistentState(false);
    const [showSolvedImages, setShowSolvedImages, clearShowSolvedImages] = usePersistentState(false);
    const [showUnsolvedImages, setShowUnsolvedImages, clearShowUnsolvedImages] = usePersistentState(true);
    const [shownImage, setShownImage] = React.useState<Image | undefined>(undefined);


    if (isLoading) {
        return <div>Loading...</div>
    }
    if (isError) {
        return <div>Error! {error.message}</div>
    }

    const posFromStr = (pos: string): Point => {
        const [lat, lon] = pos.split(',').map(parseFloat);
        return [lat, lon];
    }

    const handleSetShowSolvedImages = (event: React.ChangeEvent<HTMLInputElement>) => {
        setShowSolvedImages(event.target.checked);
    };

    const handleSetShowUnsolvedImages = (event: React.ChangeEvent<HTMLInputElement>) => {
        setShowUnsolvedImages(event.target.checked);
    };

    let img_list =
        images?.reduce((acc, sub) => {
            if (sub.claimed_team) {
                acc[0].push(sub);
            } else {
                acc[1].push(sub);
            }
            return acc;
        }, [[], []] as Image[][]);

    const setImage = (img: Image) => {
        if (shownImage?.number === img.number) {
            setShownImage(undefined);
        } else {
            setShownImage(img);
        }
    }


    return (
        // lat 49
        // long 7
        <div>
            <Map height={700} defaultCenter={[49.255280, 7.041029]} defaultZoom={15}
                provider={drawingMap ? stamenToner : osm}
            >
                {
                    showSolvedImages && img_list && img_list[0].map((img, i) => {
                        return <Marker key={i} anchor={posFromStr(img.location)} color={grayColor}
                            onClick={() => setImage(img)}
                        />
                    })
                }
                {
                    showUnsolvedImages && img_list && img_list[1].map((img, i) => {
                        return <Marker key={i} anchor={posFromStr(img.location)} color={imgColor}
                            onClick={() => setImage(img)}
                        />
                    })
                }
                {/* or use overlay  */}
                {positions!.map((pos, i) => {
                    return <Marker width={30} key={i} anchor={posFromStr(pos.location)} color={getBorderColor(i)} />
                })}
            </Map>
            {
                positions!.map((pos, i) =>
                    <ColorBox key={i} color={getBorderColor(i)} label={pos.team + " - " + diffTime(pos.timestamp)} />
                )
            }
            <div className='bottom-bar'>
                <div className="checkboxes">
                    <Checkbox checked={showUnsolvedImages} onChange={handleSetShowUnsolvedImages} /> Show unsolved image location
                    <Checkbox checked={showSolvedImages} onChange={handleSetShowSolvedImages} /> Show solved image location
                    {
                        isLoading_images ? <div>Loading images...</div> :
                            isError_images ? <div>Error with images! {error_images?.message}</div> : ""
                    }
                    <br />
                    <Checkbox checked={drawingMap} onChange={(e) => setDrawingMap(e.target.checked)} /> Map as Drawing
                </div>
                <div className='image-display'>
                    {
                        shownImage &&
                        <img src={shownImage.url} alt={"Image " + shownImage.number} height={200} />
                    }
                </div>
            </div>

        </div>
    );
}



export default Admin;