import React, { useState } from 'react';
import { BrowserRouter, Routes, Route, Outlet, Navigate } from "react-router-dom";
import './css/App.css';
import Layout from "./Layout";

import Admin from "./Components/Admin/App";
import QueueApp from "./Components/Queue/App";
import StudentApp from './Components/Student/App';

import NoPage from './Components/General/NoPage';
import Login from './Components/General/Login';
import { useSessionContext } from './SessionContext';

import ProtectedRoute, { ProtectedRouteProps } from "./ProtectedRoute";

import { QueryClient, QueryClientProvider } from "react-query";


export const queryClient = new QueryClient();

function App() {

    const [sessionContext, updateSessionContext] = useSessionContext();

    const setRedirectPath = (path: string) => {
        // useEffect(() => {
        updateSessionContext({ ...sessionContext, redirectPath: path });
        // }, []);
    }

    // if (!sessionContext.redirectPath) {
    //     // useEffect(() => {
    //     // setRedirectPath('/queue');
    //     // setRedirectPath(window.location.pathname);
    //     // }, []);
    // }

    const defaultProtectedRouteProps: Omit<ProtectedRouteProps, 'outlet'> = {
        isAuthenticated: !!sessionContext.isAuthenticated,
        authenticationPath: '/login',
        redirectPath: sessionContext.redirectPath,
        setRedirectPath: setRedirectPath
    };


    return (
        <QueryClientProvider client={queryClient}>
            <div className="wrapper">
                <Routes>
                    <Route path="/" element={<Layout />}>
                        <Route index element={<StudentApp />} />
                        <Route path='admin' element={<ProtectedRoute {...defaultProtectedRouteProps} outlet={<Admin />} />} />
                        <Route path='queue' element={<ProtectedRoute {...defaultProtectedRouteProps} outlet={<QueueApp />} />} />
                        <Route path="login" element={<Login />} />
                        <Route path="*" element={<NoPage />} />
                    </Route>
                </Routes>
            </div>
        </QueryClientProvider>
    );
}

export default App;