export const plural = (num: number): string => {
    if (num === 1) {
        return "";
    }
    return "s";
}

export const diffTime = (time: string): string => {
    const now = new Date();
    const then = new Date(time);
    const diff = now.getTime() - then.getTime();
    let diffMins = Math.round(diff / 60000);
    let outStr = "";
    if (diffMins > 60) {
        let diffHours = Math.round(diffMins / 60);
        if (diffHours > 24) {
            const diffDays = Math.round(diffHours / 24);
            diffHours = diffHours % 24;
            outStr = `${diffDays} day${plural(diffDays)}, `;
        }
        diffMins = diffMins % 60;
        outStr += `${diffHours} hour${plural(diffHours)}, `;
    }
    outStr += diffMins + " minute" + plural(diffMins) + " ago";
    return outStr;
}


