import React from 'react';
import './css/Teams.css';

import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend,
    Chart,
    ChartEvent,
    ActiveElement,
} from 'chart.js';
import pattern from 'patternomaly';
import { Bar } from 'react-chartjs-2';

import { useQuery, useMutation } from 'react-query'
import { addTeamPost, fetchTeams, query_options, Team, TeamEvent } from '../../util/Query';
import { queryClient } from '../../App';
import { getBgColor, getBorderColor } from '../../util/Const';

ChartJS.register(
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend
);


const options = {
    indexAxis: 'y' as const,
    responsive: true,
    animationEnabled: true,
    theme: "light2",
    title: {
        text: "Scores"
    },
    plugins: {
        legend: {
            display: false
        }
    }
};

interface selectTeamProp {
    selectedTeam: string;
    setSelectedTeam: (team: string) => void;
    team_events: TeamEvent[];
}

function Teams({ selectedTeam, setSelectedTeam, team_events }: selectTeamProp) {
    const { data: teams, error, isError, isLoading } = useQuery<Team[], Error>('teams', fetchTeams, query_options)
    const { mutate: addTeamMutator } = useMutation(addTeamPost, { retry: 3 })
    const teamName = React.useRef<HTMLInputElement>(null);

    if (isLoading) {
        return <div>Loading...</div>
    }
    if (isError) {
        return <div>Error! {error.message}</div>
    }

    let alertedTeams = [];
    let backgrounds = [];
    let borders = [];
    for (let i = 0; i < teams!.length; i++) {
        let team = teams![i];
        let name = team.name;
        let color: string | CanvasPattern = getBgColor(i);
        let events = team_events.filter((event) => event.team === "global" || event.team === name);
        if (events.length > 0) {
            color = pattern.draw('diagonal', color);
        } else if (team.name === selectedTeam) {
            color = pattern.draw('cross', color);
        }
        if (events.find((event) => event.blocking === 0)) {
            name = "⚠️" + name;
        }
        if (events.find((event) => event.blocking === 1)) {
            name = "🔥" + name;
        }

        if (team.name === selectedTeam) {
            name = name + "⭐";
        }
        backgrounds.push(color);
        borders.push(getBorderColor(i));
        alertedTeams.push(name);
    }

    const clickHandler = (event: ChartEvent, elements: ActiveElement[], chart: Chart) => {
        console.log("clickHandler");
        if (elements.length > 0) {
            console.log(elements[0].index);
            console.log(teams![elements[0].index]);
            setSelectedTeam(teams![elements[0].index].name);
        }
    };

    const diagram_data = {
        labels: alertedTeams,
        datasets: [
            {
                label: 'Score',
                data: teams!.map(t => t.score),
                borderWidth: 1,
                backgroundColor: backgrounds,
                borderColor: borders,
            }
        ],
    };

    const addTeam = () => {
        let name = teamName.current?.value;
        if (name) {
            console.log("Adding team " + name);
            if (!teams!.map(t => t.name).includes(name)) {
                addTeamMutator(name);
                setTimeout(
                    () => {
                        queryClient.invalidateQueries(['teams']);
                        queryClient.refetchQueries(['teams']);
                    }, 500);
            }
        }
    };

    return (
        <div className="Teams">
            <Bar options={
                { ...options, onClick: clickHandler }
            } data={diagram_data}
            />
            {/* TODO: add css */}
            {/* <input type="text" placeholder='team name' ref={teamName} /> */}
            {/* <input type="button" value="Add team" onClick={addTeam} /> */}
        </div>
    );
}

export default Teams;
