import './css/Images.css';

import Carousel from 'better-react-carousel';
import { fetchImages, fetchTeam_curry, Image, query_options, Team, TeamEvent } from '../../util/Query';
import { useQuery } from 'react-query';

interface imgProp {
    team: string;
    // team_events: TeamEvent[];
}


function Images({ team }: imgProp) {
    const { data: team_info, error, isError, isLoading } = useQuery<Team, Error>(['team_info', team], fetchTeam_curry(team), query_options)
    // const { data: team_events, error: error_events, isError: isError_events, isLoading: isLoading_events } = useQuery<TeamEvent, Error>('team_events', , query_options)
    // const { data: event, error: error_event, isError: isError_event, isLoading: isLoading_event } = useQuery<TeamEvent, Error>(['team_event', team_info?.event], fetchEvent_curry(team_info?.event), query_options_no_refetch)
    const { data: images, error: error_images, isError: isError_images, isLoading: isLoading_images } = useQuery<Image[], Error>('images', fetchImages, query_options)
    if (isLoading) {
        return <div>Loading...</div>
    }
    if (isError) {
        return <div>Error! {error.message}</div>
    }
    if (!team_info) {
        return <div>Issue with team info</div>
    }
    // team might be {}
    console.log("team_info:", team_info);
    console.log("name:", team_info.name);
    // if (team_info.event) {
    //     if (isLoading_event) {
    //         return <div>Loading...</div>
    //     }
    //     if (isError_event) {
    //         return <div>Error! {error_event.message}</div>
    //     }
    //     if (!event) {
    //         return <div>Error retrieving event</div>
    //     }
    //     return (
    //         <div className="event">
    //             <h1>
    //                 ⚠️ Warning: {event.name} ⚠️
    //             </h1>
    //             <p>
    //                 {event.description}
    //             </p>
    //         </div>
    //     );
    // }

    if (isLoading_images) {
        return <div>Loading...</div>
    }
    if (isError_images) {
        return <div>Error! {error_images.message}</div>
    }
    if (!images) {
        return <div>Issue with loading images</div>
    }

    console.log("images:", images);

    let grouped_images = new Map<number, Image[]>();
    for (let i = 0; i < images.length; i++) {
        let image = images[i];
        if (!grouped_images.has(image.difficulty)) {
            grouped_images.set(image.difficulty, []);
        }
        grouped_images.get(image.difficulty)?.push(image);
    }

    const difficultyToString = (cur_difficulty: number) => {
        switch (cur_difficulty) {
            case 0:
                return "Easy";
            case 1:
                return "Medium";
            case 2:
                return "Hard";
            default:
                return "Unknown";
        }
    };
    const difficultyToEmoji = (cur_difficulty: number) => {
        let s = "";
        for (let i = 0; i < cur_difficulty + 1; i++) {
            s += "🌶";
        }
        return s;
    };

    return (
        <div className="Images">
            {
                Array.from(grouped_images.keys()).map((cur_difficulty) => {
                    let images = grouped_images.get(cur_difficulty)!;
                    let cols = Math.min(4, images.length);
                    let rows = Math.min(3, Math.ceil(images.length / cols));
                    return (
                        <div className="imageGallery">
                            <h2 id={difficultyToString(cur_difficulty).toLowerCase()}>{difficultyToEmoji(cur_difficulty)} {difficultyToString(cur_difficulty)}</h2>
                            <Carousel cols={cols} rows={rows} gap={10} loop>
                                {
                                    images.map((image) =>
                                        <Carousel.Item key={image.number}>
                                            <div className="head-text">
                                                <div className="head-image">
                                                    <img width="100%" src={image.url} alt={"number_" + image.number} className={(image.claimed_team ? "finished" : "")} />
                                                </div>
                                                <div className="top-left-text">
                                                    {image.number}
                                                </div>
                                                <div className="top-right-text">
                                                    {image.claimed_team}
                                                </div>
                                                <div className="bottom-left-text">
                                                    {image.attempts > 0 ? "❌ " + image.attempts : ""}
                                                </div>
                                            </div>
                                        </Carousel.Item>
                                    )
                                }
                            </Carousel>
                        </div>
                    );
                })
            }
        </div>
    );
}

export default Images;
