import React, { ChangeEvent, ChangeEventHandler, useEffect } from 'react';
import ReactCompareImage from 'react-compare-image';
import { MutationFunction, UseMutateFunction } from 'react-query';
import { Judgement, Submission, SubmissionEvent } from '../../util/Query';
import './css/SubmissionBox.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCheck, faCheckDouble, faCoffee, faCross, faImage, faPaperPlane, faStar, faStarOfLife, faTextWidth, faX, faXmark } from '@fortawesome/free-solid-svg-icons'

import { Icon, Button, Input, Tooltip } from "@material-ui/core";
import { queryClient } from '../../App';
import { diffTime } from '../../util/Const';

interface Props {
    submission: Submission;
    mutator: UseMutateFunction<void, unknown, Judgement, unknown>;
    events: SubmissionEvent[];
}


function SubmissionBox({ submission, mutator, events }: Props) {
    const red = "#d9534f";
    const green = "#5cb85c";
    const blue = "#5bc0de";
    let initMsg = submission.message ? submission.message : "";
    let [message, setMessage] = React.useState(initMsg);

    useEffect(() => {
        setMessage(initMsg);
    }, [initMsg]);

    const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
        setMessage(e.target.value);
    };
    const submit = (s: string) => {
        // setMessage(s);
        mutator({ id: submission.id, number: submission.image, message: s });
        queryClient.invalidateQueries(['submissions']);
        queryClient.refetchQueries(['submissions']);
    };
    return (
        <div className={`box ${submission.message ? 'answered' : ''} ${events.length > 0 ? 'event-box' : ''}`}>
            {/* <div className='teamname'>{submission.team}</div> */}
            <div className="head-text">
                <div className="head-image">
                    <img src={submission.url} alt="submission" />
                </div>
                <div className="bottom-left-text">
                    Submission for {submission.image}
                </div>
                <div className="top-left-text">
                    {submission.team}
                </div>
            </div>
            {
                submission.image === -1 ?
                    <div className="event-text">
                        Event
                    </div>
                    :
                    <>
                        <div className="head-text">
                            <div className="head-image">
                                <img src={submission.reference} alt="reference" />
                            </div>
                            <div className="bottom-left-text">
                                Reference
                            </div>
                        </div>
                        <div className="compare-box">
                            <ReactCompareImage leftImage={submission.reference} rightImage={submission.url}
                                leftImageLabel="Submission" rightImageLabel="Reference" />
                        </div>
                    </>
            }
            {/* { submission.message ?
                    <div className='message'>{submission.message}
                    </div> :
                    ''
            } */}
            <Tooltip title="Accept the image">
                <Button className='button' style={{ backgroundColor: green }}
                    onClick={() => submit("accepted")}>
                    <FontAwesomeIcon icon={faCheck} />
                </Button>
            </Tooltip>
            <table>
                <tr>
                    <td colSpan={3}>
                        {diffTime(submission.timestamp)}
                    </td>
                </tr>
                <tr>
                    <td>
                        <Tooltip title="Wrong background / location">
                            <Button className='button' style={{ backgroundColor: red, fontSize: "1.5rem" }}
                                onClick={() => submit("wrong: background")}>
                                <FontAwesomeIcon icon={faImage} />
                            </Button>
                        </Tooltip>
                    </td>
                    <td>
                        <Tooltip title="Extra element is missing">
                            <Button className='button' style={{ backgroundColor: red, fontSize: "1.5rem" }}
                                onClick={() => submit("wrong: missing element")}>
                                <FontAwesomeIcon icon={faStar} />
                            </Button>
                        </Tooltip>
                    </td>
                    <td>
                        <Tooltip title="Generally wrong">
                            <Button className='button' style={{ backgroundColor: red, fontSize: "1.5rem" }}
                                onClick={() => submit("wrong: image")}>
                                <FontAwesomeIcon icon={faXmark} />
                            </Button>
                        </Tooltip>
                    </td>
                </tr>
                <tr>
                    <td colSpan={2}>
                        <Input placeholder="Message" value={message} onChange={handleChange} />
                    </td>
                    <td>
                        <Tooltip title="Send custom Message">
                            <Button className='button' style={{ backgroundColor: blue, fontSize: "1.5rem" }}
                                onClick={() => submit("wrong: " + message)}>
                                <FontAwesomeIcon icon={faPaperPlane} />
                            </Button>
                        </Tooltip>
                    </td>
                </tr>
            </table>

            {
                events.length > 0 ?
                    <div className="event-text-box">
                        {
                            events.map((e, i) =>
                                <div key={i}>
                                    Event {e.event}: {e.name} <br />
                                    Blocking: {e.blocking ? "Yes" : "No"} <br />
                                    <div className='description'> {e.description} </div>
                                </div>
                            )
                        }
                    </div>
                    :
                    <></>
            }
            {/* <FontAwesomeIcon icon={faCoffee} /> */}
            {/* <p>{submission.message}</p> */}
        </div>
    );
}

export default SubmissionBox;