import { useRef, useState } from "react";
import { useNavigate } from "react-router";
import { useSessionContext } from "../../SessionContext";
import { PWD_HASH } from "../../util/Const";
import bcrypt from "bcryptjs";
import './css/Login.css';

export default function Login() {
    const [session, setSession] = useSessionContext();
    const navigate = useNavigate();
    const [password, setPassword] = useState('');
    const handlePasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setPassword(e.target.value);
    };
    const pwdRef = useRef<HTMLInputElement>(null);

    const handleLogin = async () => {
        if (!PWD_HASH) {
            alert("No password set");
            setSession({ ...session, isAuthenticated: true });
            navigate(session.redirectPath);
            return;
        }
        if (bcrypt.compareSync(password, PWD_HASH)) {
            setSession({ ...session, isAuthenticated: true });
            navigate(session.redirectPath);
        } else {
            // alert('Incorrect password');
            if (pwdRef.current) {
                pwdRef.current.focus();
                pwdRef.current.select();
                pwdRef.current.setSelectionRange(0, 99999);
                pwdRef.current.className = 'incorrect';
            }
        }
    }

    return (
        <div className="login-wrapper">
            <h1>Please Log In</h1>
            <div>
                <label>
                    <p>Password</p>
                    <input type="password" id="password" value={password} onChange={handlePasswordChange} ref={pwdRef} />
                </label>
                <div>
                    <button onClick={handleLogin}>Login</button>
                </div>
            </div>
        </div>);
}