import axios from "axios";
import { API_URL } from "./Const";

export const query_options = {
    refetchInterval: 5000,
}
export const query_options_no_refetch = {}

export interface Team {
    score: number;
    name: string;
}

export interface Event {
    event: string;
    name: string;
    description: string;
    blocking: number;
    effect: string;
}

export interface TeamEvent extends Event {
    team: string;
}

export interface Image {
    number: number,
    url: string,
    location: string,
    difficulty: number,
    // changed: string,
    filename: string,
    // pseudo elements:
    claimed_team: string,
    attempts: number,
}

export interface SubmissionEvent extends Event {
    submission: number,
}

export interface Submission {
    id: number,
    team: string,
    image: number,
    url: string,
    timestamp: string,
    message: string,
    reference: string
}

export interface Judgement {
    id: number,
    number: number,
    message: string,
}

export interface TeamPos {
    location: string,
    team: string,
    timestamp: string,
    number: number,
}

export async function fetchImages() {
    const { data } = await axios.get(API_URL + '/images')
    return data
}
export async function fetchLocations() {
    const { data } = await axios.get(API_URL + '/locations')
    return data
}
export async function fetchTeams() {
    const { data } = await axios.get(API_URL + '/teams')
    return data
}
export async function fetchSubmissions() {
    const { data } = await axios.get(API_URL + '/submissions')
    return data
}
export async function fetchSubmissionEvents() {
    const { data } = await axios.get(API_URL + '/submission/events')
    return data
}
export async function fetchTeamEvents() {
    const { data } = await axios.get(API_URL + '/team_events')
    return data
}

export async function addTeamPost(team_name: string) {
    const response = await axios.post(API_URL + '/teams/add',
        {
            name: team_name,
        });
    console.log(response.data)
}
export async function judgeSubmission(j: Judgement) {
    const response = await axios.post(API_URL + '/submission/judge', j);
    console.log(response.data)
}

