export function fetchTeam_curry(team_name: string) {
    return async function () {
        const { data } = await axios.post(API_URL + '/teams/get',
            {
                name: team_name,
            });
        return data;
    }
}
export async function fetchTeam(team_name: string) {
    const { data } = await axios.post(API_URL + '/teams/get',
        {
            name: team_name,
        });
    return data;
}