import React from 'react';
import { useMutation, useQuery } from 'react-query';
import { fetchSubmissionEvents, fetchSubmissions, judgeSubmission, query_options, Submission, SubmissionEvent } from '../../util/Query';
import SubmissionBox from './SubmissionBox';

function Queue() {
    const { data: submissions, error, isError, isLoading } = useQuery<Submission[], Error>('submissions', fetchSubmissions, { refetchInterval: 200 })
    const { data: submissionEvents, error: error_events, isError: isError_events, isLoading: isLoading_events } = useQuery<SubmissionEvent[], Error>('submissionEvents', fetchSubmissionEvents, query_options)
    const { mutate: judgeMutator } = useMutation(judgeSubmission, { retry: 3 })

    // console.log(submissionEvents);

    if (isLoading) {
        return <div>Loading...</div>
    }
    if (isError) {
        return <div>Error! {error.message}</div>
    }

    let [answeredSubmissions, unansweredSubmissions] =
        submissions!.reduce((acc, sub) => {
            if (sub.message) {
                acc[0].push(sub);
            } else {
                acc[1].push(sub);
            }
            return acc;
        }, [[], []] as Submission[][]);

    unansweredSubmissions.reverse();

    // console.log("answeredSubmissions", answeredSubmissions);
    // console.log("unansweredSubmissions", unansweredSubmissions);

    return (
        <div>
            {
                unansweredSubmissions.map((sub, i) =>
                    <SubmissionBox key={"unanswered_" + i} submission={sub} mutator={judgeMutator}
                        events={submissionEvents ?
                            submissionEvents.filter((event) => event.submission === sub.id) : []}
                    />
                )
            }
            {
                answeredSubmissions.map((sub, i) =>
                    <SubmissionBox key={"answered_" + i} submission={sub} mutator={judgeMutator}
                        events={submissionEvents ?
                            submissionEvents.filter((event) => event.submission === sub.id) : []}
                    />
                )
            }
        </div>
    );
}

export default Queue;